import React, { useEffect } from 'react'
import ServicesFourthSection from './ServicesFourthSection'
import AMCBenefits from './AMC'
import Steps from './Steps'
import AdvantageCard from './Advantage/AdvantageCard'
import Partners from './Partners/Partners'
import Customer from './Customers/Customer'
import Offers from './Offered/Offer'
import Banner from './Banner/Banner'
import Footer from '../Footer/Footer'

const Services = () => {
  useEffect(()=>{
    window.scrollTo(0,0)
  })
  return (
    <div className='container-fluid p-0 m-0'>
      <Banner />
     <Offers />  
      <AMCBenefits />
      <Steps />
      <AdvantageCard />
      <Partners  title={"Our OEM Partners"} para={"From Machines to tools to finance everything you need in one place"}/>
      <Customer />
  <ServicesFourthSection/>
      <Footer/>
    </div>
  )
}

export default Services